import React, { useEffect, useState } from 'react';
import AccountsTab from './AccountsTab';
import { useHistory } from 'react-router-dom';
import { useLocation } from 'react-router-dom';

const AccountsTabBar = () => {

    const [tabUrlTabNameMap, setTabUrlTabNameMap] = useState([
        { tabName: "Ebay Accounts", tabUrl: "/accounts/ebayAccounts" },
        { tabName: "Gmail Accounts", tabUrl: "/accounts/gmailaccounts" }])

    let history = useHistory();
    const location = useLocation();
    const [selectedTab, setSelectedTab] = useState({});

    useEffect(() => {

        setSelectedTab({ tabName: tabUrlTabNameMap.filter(datum => location.pathname.includes(datum.tabUrl) && datum.tabUrl.length != 1)[0].tabName, modifyUrl: true });
    
      }, []);

    useEffect(() => {

        switch (selectedTab.tabName) {
          case "Ebay Accounts": if (selectedTab.modifyUrl) history.push('/accounts/ebayAccounts');
            break;
          case "Gmail Accounts": if (selectedTab.modifyUrl) history.push('/accounts/gmailaccounts');
            break;
        }
    
      }, [selectedTab]);

    return (
        <div className="container-fluid">
            <div className="row">
                <div className="col-2"></div>
                <div className="col-8 d-flex justify-content-start">
                    <AccountsTab tabName="Ebay Accounts" selectedTab={selectedTab} setSelectedTab={setSelectedTab}/>
                    <AccountsTab tabName="Gmail Accounts" selectedTab={selectedTab} setSelectedTab={setSelectedTab}/>
                </div>
            </div>
        </div>);
}

export default AccountsTabBar;