import React from 'react';
import './AccountsTab.css';

const AccountsTab = (props) => {

    return (
        <div className={props.selectedTab.tabName == props.tabName ? "accounts-tab accounts-tab-selected" : "accounts-tab"} onClick={() => props.setSelectedTab({tabName: props.tabName, modifyUrl: true})}>
            <div className="main-sidebar-tab-image-text-chevron">
                <div style={{display: 'inline-block'}}>{props.tabName}</div>
            </div>
        </div>
    );
}

export default AccountsTab;